import React, { useState } from 'react';
import { Add } from '@material-ui/icons';
import './add.style.scss';
import { Fab, Zoom, ClickAwayListener } from '@material-ui/core';

export const AddText = (props) => {

    const [Title, setTitle] = useState("");
    const [Content, setContent] = useState("");
    const [isZoom, setisZoom] = useState(false);

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;

        if (name === 't') {
            setTitle(value);
        }
        else {
            setContent(value);
        }

    }

    return (
        <ClickAwayListener onClickAway={() => { setisZoom(false) }}>
            <form className='create-note'>
                <input type='text'
                    style={{ display: !isZoom && 'none' }}
                    onChange={handleChange}
                    name='t'
                    placeholder='Title'
                    value={Title} />

                <textarea type='text'
                    onClick={() => setisZoom(true)}
                    onChange={handleChange}
                    name='c'
                    placeholder='Take a note'
                    rows={isZoom ? '3' : '1'}
                    value={Content} />

                <Zoom in={true}>
                    <Fab style={{ display: !isZoom && 'none' }} className='button' onClick={(event) => {
                        if (Title !== "" || Content !== "")
                            props.onClick(Title, Content);
                        setTitle("");
                        setContent("");
                        event.preventDefault();
                    }}><Add /></Fab>
                </Zoom>
            </form>
        </ClickAwayListener>
    )
}