import { useState } from 'react';
import { AddText } from './components/add/add.component';
import Footer from './components/footer/footer.cmponent';
import Header from './components/header/header.component';
import Note from './components/note/note.component';

function App() {

  const [kItems, setkItems] = useState([]);

  const handleClick = (Title, Content) => {
    setkItems([...kItems, {
      key: kItems.length,
      title: Title,
      content: Content,
    }])
  }

  const deleteItem = (id) => {
    setkItems(() => {
      return kItems.filter((kItem, index) => {
        return index !== id;
      });
    });
  }

  return (
    <div>
      <Header />
      <AddText onClick={handleClick} />
      <div style={{marginBottom:'20px'}}>
      {
        kItems.map(kItems => (
          <Note key={kItems.key} id={kItems.key} title={kItems.title} content={kItems.content} onDelete={deleteItem} />
        ))
      }
      </div>
      <Footer />
    </div>
  );
}

export default App;
