import { Delete } from '@material-ui/icons';
import React from 'react';
import './note.style.scss';

const Note = (props) => {
    return (
        <div className='note'>
            <h1>{props.title}</h1>
            <p>{props.content}</p>
            
            <button onClick={() => {
                return props.onDelete(props.id);
            }}
            ><Delete /></button>
        </div>
    )
}

export default Note;